<template>
    <div class="listMenu">
        <v-select v-bind:value="field" @input="commit" :options="fields" :clearable="false"></v-select>
        <button v-bind:value="dir" @click="commitDir" :title="$t('household.buttons.sorting.' + dir)">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512" v-bind:svg-inline="''" v-if="isASC" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M187.298 395.314l-79.984 80.002c-6.248 6.247-16.383 6.245-22.627 0L4.705 395.314C-5.365 385.244 1.807 368 16.019 368H64V48c0-8.837 7.163-16 16-16h32c8.837 0 16 7.163 16 16v320h47.984c14.241 0 21.363 17.264 11.314 27.314zm119.075-180.007A12 12 0 01294.838 224h-35.717c-8.22 0-14.007-8.078-11.362-15.861l57.096-168A12 12 0 01316.217 32h39.566c5.139 0 9.708 3.273 11.362 8.139l57.096 168C426.886 215.922 421.1 224 412.879 224h-35.735a12 12 0 01-11.515-8.622l-8.301-28.299h-42.863l-8.092 28.228zm22.857-78.697h13.367l-6.6-22.937-6.767 22.937zm12.575 287.323l67.451-95.698a12 12 0 002.192-6.913V300c0-6.627-5.373-12-12-12H274.522c-6.627 0-12 5.373-12 12v28.93c0 6.627 5.373 12 12 12h56.469c-.739.991-1.497 2.036-2.27 3.133l-67.203 95.205a12.001 12.001 0 00-2.196 6.92V468c0 6.627 5.373 12 12 12h129.355c6.627 0 12-5.373 12-12v-28.93c0-6.627-5.373-12-12-12h-61.146c.74-.993 1.5-2.039 2.274-3.137z"/></svg>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512" v-bind:svg-inline="''" v-if="isDESC" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M4.702 116.686l79.984-80.002c6.248-6.247 16.383-6.245 22.627 0l79.981 80.002c10.07 10.07 2.899 27.314-11.314 27.314H128v320c0 8.837-7.163 16-16 16H80c-8.837 0-16-7.163-16-16V144H16.016c-14.241 0-21.363-17.264-11.314-27.314zm301.671 98.621A12 12 0 01294.838 224h-35.717c-8.22 0-14.007-8.078-11.362-15.861l57.096-168A12 12 0 01316.217 32h39.566c5.139 0 9.708 3.273 11.362 8.139l57.096 168C426.886 215.922 421.1 224 412.879 224h-35.735a12 12 0 01-11.515-8.622l-8.301-28.299h-42.863l-8.092 28.228zm22.857-78.697h13.367l-6.6-22.937-6.767 22.937zm12.575 287.323l67.451-95.698a12 12 0 002.192-6.913V300c0-6.627-5.373-12-12-12H274.522c-6.627 0-12 5.373-12 12v28.93c0 6.627 5.373 12 12 12h56.469c-.739.991-1.497 2.036-2.27 3.133l-67.203 95.205a12.001 12.001 0 00-2.196 6.92V468c0 6.627 5.373 12 12 12h129.355c6.627 0 12-5.373 12-12v-28.93c0-6.627-5.373-12-12-12h-61.146c.74-.993 1.5-2.039 2.274-3.137z"/></svg>
        </button>
    </div>
</template>

<script>
    import { mapGetters, mapActions } from 'vuex'
    import vSelect from 'vue-select'

    export default {
        name: "ListMenu",
        components: { vSelect },
        props: {
            "fields": {
                "type": Array,
                "required": true
            },
            "store": {
                "type": String,
                "required": true
            }
        },
        data () {
            return {
                "field": null,
                "dir": "ASC"
            }
        },
        computed: {
            sort () {
                return this.$store.getters[this.store + "/sort"]
            },
            isASC () {
                return this.sort.dir === "ASC"
            },
            isDESC () {
                return this.sort.dir === "DESC"
            }
        },
        created () {
            this.field = this.fields.find(f => f.value === this.sort.field)
            this.dir = this.sort.dir
        },
        methods: {
            sortAction (sorting) {
                this.$store.dispatch(this.store + '/sort', sorting)
            },
            commit: function (event) {
                this.field = event
                var sorting = {
                    "field": this.field.value,
                    "dir": this.dir
                }
                this.sortAction(sorting)
            },
            commitDir: function (event) {
                if (event.target.value === "ASC") {
                    this.dir = "DESC"
                } else {
                    this.dir = "ASC"
                }
                var sorting = {
                    "field": this.field.value,
                    "dir": this.dir
                }
                this.sortAction(sorting)
            }
        }
    }
</script>

